<template>
  <div class="container">
    <ul>
      <li v-for="(item, index) in codeList">{{ item }}</li>
    </ul>
    <input class="code-input" type="text" ref="codeInput" v-model="code" @focus="focusCode"
      @blur="$event.target.focus()" @change="addCode" v-focus>
  </div>
</template>

<script>
export default {
  data() {
    return {
      code: '',
      codeList: []
    }
  },
  methods: {
    addCode() {
      this.codeList.push(this.code)
      this.code = ''
    },
    focusCode() {
      //一旦聚焦就设属性为只读，防止弹出键盘
      this.$refs.codeInput.setAttribute('readonly', 'readonly');
      //200ms后删除只读属性，扫码枪可输入
      setTimeout(() => {
        this.$refs.codeInput.removeAttribute('readonly');
      },200)
    }
  },
}
</script>

<style lang="scss" scoped>
.code-input {
  opacity: 0;
}
</style>
